import React, { Component } from 'react';

const TablaDatos = (props) => {

    console.log(props.vehiculos);
    return (
        <table>
            <thead>
                <tr>
                    <th>☆☆ Marca ☆☆</th>
                    <th>☆☆ Modelo ☆☆</th>
                    <th>☆☆ Opciones ☆☆</th>
                </tr>
            </thead>
            <tbody>
                {
                    props.vehiculos.length > 0 ?
                        props.vehiculos.map(vehiculos => (
                            <tr key={vehiculos.id}>
                                <td>{vehiculos.marca}</td>
                                <td>{vehiculos.modelo}</td>
                                <td>
                                    <button className="button muted-button"
                                        onClick={
                                            () => {
                                                props.editado(vehiculos)
                                            }
                                        }
                                    >
                                        ✏️
                                        </button>
                                    <button
                                        className="button muted-button"
                                        onClick={() => { props.eliminar(vehiculos.id) }}
                                    >
                                        ❌
                                        </button>
                                </td>
                            </tr>
                        )) : (
                            <tr>
                                <td colSpan={3}>🧩 No Hay Vehiculos Registrados</td>
                            </tr>
                        )}
            </tbody>
        </table>
    );
}

export default TablaDatos;
