import React from 'react';
import { useForm } from 'react-hook-form';

const EditVehiculo = (props) => {

    const { register, formState: { errors }, handleSubmit, setValue } = useForm({
        defaultValues: props.vehiculoAct
    });

    setValue('marca', props.vehiculoAct.nombre);
    setValue('modelo', props.vehiculoAct.edad);

    const onSubmit = (data, e) => {
        console.log(data);
        data.id = props.vehiculoAct.id
        props.vehiculoActualizado(props.vehiculoAct.id, data)
        e.target.reset();
    }
    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <label>☆☆☆ Marca ☆☆☆</label>
            <input type="text" {...register("marca", { required: true })} />
            {errors.marca && "Campo requerido"}
            <label>☆☆☆ Modelo ☆☆☆</label>
            <input type="text" {...register("modelo", { required: true })} />
            {errors.modelo && "Campo requerido"}
            <button> ☑️ </button>
        </form>
    );
}

export default EditVehiculo;